
.nav-container {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    padding: 25px 50px;
    box-sizing: border-box;
    z-index: 10;
}

nav {
    display: flex;
    justify-content: flex-end;
    align-items: flex-start;
}

nav a {
    font-size: 1em;
    color: #333332;
    cursor: pointer;
    padding: 0px;
    margin: 0px;
    padding: 5px 10px;
    border-bottom: 3px solid transparent;
    text-decoration: none;
}

nav a:last-child {
    padding-right: 0px;
}

nav a:hover {
    border-bottom: 3px solid black;
}

.active {
    border-bottom: 3px solid #b6a590;
}


@media only screen and (max-width: 500px) {
    .nav-container {
        padding: 20px;
    }

    nav {
        display: flex;
        justify-content: center;
        width: 100%;
    }

    nav a {
        padding-bottom: 10px;
    }

    nav a:first-child {
        padding-left: 0px;
    }
}