
.single-project {
    height: 100%;
    max-width: 100vw;
    margin-top: 0px;
    padding-top: 10px;
    box-sizing: border-box;
    position: relative;
    will-change: margin;
}

.project-image {
    width: 90%;
    max-width: 950px;
    height:fit-content;
    min-height: 500px;
    position: relative;
    z-index: 0;
    left: 50%;
    top: -150px;
    transform: translateX(-50%);
    transition: top 0.5s ease;
}

.project-image img {
    width: 100%;
    border-radius: 10px;
}

.project-description {
    width: 80%;
    max-width: 900px;

    font-size: .8em;
    line-height: 1.5em;

    background: black;

    padding: 50px;
    box-sizing: border-box;

    border-radius: 5px;

    position: absolute;
    left: 50%;
    bottom: 50px;
    transform: translateX(-50%);
}

.project-description h2 {
    margin-top: 0px;
}

.show {
    top: 0px;
}

.project-description a, .info-content a {
    text-decoration: none;
    color: #DFCEBA;
    text-align: right;
    display: block;
    padding-top: 20px;
}

.project-description a:hover, .info-content a:hover {
    color: white;
}


@media only screen and (max-width: 500px) {
    .project-description {
        width: 95%;
    }

    .project-image {
        width: 98%;
    }

    .project-description {
        padding: 30px;
    }

    .show {
        margin-top: 0px;
    }

    .links {
        padding-top: 20px;
        width: 250px;
        margin: auto;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
    }
}

@media only screen and (max-width: 414px) and (-webkit-min-device-pixel-ratio:0){
    .project-description {
        bottom: 130px;
    }
}