
.all-projects-description{
    width: 90%;
    max-width: 700px;
    margin: auto;
    padding: 100px 0px;
    box-sizing: border-box;
}

@media only screen and (max-width: 500px) {
    .all-projects-description{
        width: 90%;
    }
}

@media only screen and (max-width: 350px) {
    .all-projects-description{
        width: 90%;
        text-align: center;
    }
}