
body {
  background: #DFCEBA;
  color: #11100E;

  Font-family: system-ui, Helvetica, "Helvetica Neue", "Franklin Gothic", "ITC Franklin Gothic",Arial ,sans-serif;
  font-size: 25px;
  line-height: 40px;
}

::selection {
  background-color: red;
}

/* for link to other pages in footer */

.outro {
  margin: 150px auto;
  text-align: center;
  font-size: 1.2em;
  color: #665a4b;
  width: fit-content;
  max-width: 90%;
  box-sizing: border-box;
  border-radius: 5px;
}

.outro a {
  background: #c7b59e;
  color: #ece3d7;
  padding: 20px;
  margin: 10px auto;
  width: fit-content;
  border-radius: 5px;
}

.outro a:hover {
  color: white;
}

.page-switcher {
  text-decoration: none;
  display: block;
  padding: 10px 0px;
  box-sizing: border-box;
}

@media only screen and (max-width: 500px) {
  body {
    font-size: 20px;
  }
}

@media only screen and (max-width: 450px) {
  body {
    font-size: 18px;
  }
}

@media only screen and (max-width: 450px) {
  body {
    font-size: 16px;
    line-height: 20px;
  }
}

@media only screen and (max-width: 350px) {
  .outro {
    font-size: 0.9em;
  }
}