
.about-content {
    width: 90%;
    max-width: 700px;
    margin: auto;
    padding: 20px 0px 0px;
    box-sizing: border-box;
}

.highlight {
    text-align: center;
    font-size: 1.5em;
    line-height: 1.25em;
    padding: 50px 0px;
}

.me {
    width: 100%;
}

@media only screen and (max-width: 500px) {
    .about-content {
        width: 90%;
        padding: 20px 0px;
        line-height: 2em;
    }

    .highlight {
        padding: 25px 0px;
    }

}