
.single-project-page {
    width: 100%;
    box-sizing: border-box;
    margin-top: -10px;
    clip-path: polygon(0 0, 100% 10px, 100% 100%, 0% 100%);
    display: flex;
    flex-direction: row;
}

.single-project-page:nth-child(1){
    margin-top: 0px;
}

.single-project-page:nth-child(even){
    background: gray;
}

.single-project-page-image {
    width: 50%;
    box-sizing: border-box;
    overflow: hidden;
    position: relative;
}

.single-project-page-description {
    width: 50%;
    padding: 30px 30px 40px;
    box-sizing: border-box;
}

.single-project-page-description h2 {
    margin: 0px;
    padding: 0px;
    padding-top: 20px;
}

.single-project-page-description h3 {
    font-size: 0.8em;
    font-weight: normal;
    font-style: italic;
    margin: 0px;
    padding: 0px;
}

.links svg {
    fill: white;
    width: 30px;
    height: 30px;
    margin-right: 10px;
}

.links a {
    text-decoration: none;
    color: white;
    display: flex;
    align-items: center;
    padding-bottom: 10px;
}

@media only screen and (max-width: 500px) {
    .single-project-page {
        flex-direction: column;
    }

    .single-project-page-image {
        width: 100%;
        height: 200px;
    }

    .single-project-page-description {
        width: 100%;
    }
}