
.intro {
    display: flex;
    flex-direction: column;
    box-sizing: border-box;
}

.intro h1 {
    font-size: 4em;
    line-height: 1em;
    margin: 0px;
}

.intro-wrapper {
    height: -webkit-fill-available;
    height: 100%;

    display: flex;
    align-items: center;
    justify-content: center;
    transform: translateY(-100px);
}

.intro-content{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    margin: auto;
    width: 90%;
    max-width: 900px;
}

.contact svg {
    width: 40px;
    height: 40px;
    padding: 5px;
    padding-bottom: 15px;
    border-bottom: 3px solid #DFCEBA;
}

.contact svg:hover {
    border-bottom: 3px solid black;
}


/* Counters */

.project-count {
    width: fit-content;
    text-align: center;
    display: inline-block;
    margin-right: 40px;
}

.project-count p {
    width: 100%;
    font-size: 1em;
    font-weight: bold;
    margin: 0px;
    margin-bottom: -10px;
    padding: 0px;
}

.project-count span {
    font-size: 3em;
    font-weight: bold;
    line-height: 1em;
    display: block;
}

@media only screen and (max-width: 500px) {
    .intro h1 {
        font-size: 3em;
    }
}

@media only screen and (max-width: 380px) {
    .intro h1 {
        font-size: 2em;
    }

    .intro {
        width: 90%;
        margin: auto;
    }

    .intro-content {
        align-items: center;
    }

    .intro-content p {
        text-align: center;
    }

    .contact svg {
        width: 30px;
        height: 30px;
        padding: 4px;
    }
}

@-moz-document url-prefix() { 
    .intro-wrapper {
        height: 100vh;
    }
}