
.info {
    height: 100vh;
    padding: 100px;
    box-sizing: border-box;
    display: flex;
    justify-content: center;
    align-items: center;
    background: url('./collage.jpg');
    background-position: top left;
}

.info-content {
    background: black;
    color: #DFCEBA;
    padding: 50px;
    box-sizing: border-box;
    border-radius: 5px;
    font-size: .8em;
    line-height: 1.5em;
    max-width: 800px;
    width: 95%;
}

.info-content h2 {
    font-size: 1.5em;
    margin: 0px;
    margin-bottom: 20px;
}

@media only screen and (max-width: 500px) {
    .info {
        padding: 10px;
    }
}